:root {
  --stardimension: 16px;
}

body {
  margin: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("images/star-active.svg");
  background-repeat: no-repeat;
  background-position: center;
  height: var(--stardimension);
  width: var(--stardimension);
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--0 .stars__star,
.stars--1 .stars__star:nth-child(n + 2),
.stars--3 .stars__star:nth-child(n + 4),
.stars--4 .stars__star:nth-child(n + 5),
.stars--2 .stars__star:nth-child(n + 3) {
  background-image: url("images/star.svg");
}
